import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import { AdminView } from '@miq/adminjs';
import { AvatarGroup } from '@miq/components';
import { formatDate } from '@miq/utils';

import DocuFileGrid from '../docufile/FileGrid';
import { docServices, mapDocumentUser2Avatar } from './utils';
import { BackToStaffDocumentsLink } from '../components';

import './readonly-view.scss';
import { DocumentArchiveIconButton } from './components';

const propTypes = {
  match: PropTypes.object,
};

export default function DocumentReadOnlyView(props) {
  const { docSlug } = props.match.params;

  const [document, setDocument] = useState(null);
  const [, setError] = useState(null);

  useEffect(() => {
    docServices
      .detail(docSlug)
      .then((data) => {
        setDocument(data);
      })
      .catch((err) => setError('Error'));
  }, []);

  if (!document || !document.slug) return null;

  return (
    <AdminView
      id="DocumentReadOnlyView"
      className="document-readonly-view"
      title={<BackToStaffDocumentsLink />}
      actions={
        <DocumentArchiveIconButton
          {...props}
          document={document}
          onSuccess={(data) => setDocument({ ...document, ...data })}
          className="btn-primary-3"
        />
      }
    >
      <AdminView.Section
        title={document.title}
        text={formatDate(document.created, { month: 'long', day: 'numeric', year: 'numeric' }, 'fr')}
        actions={<AvatarGroup users={mapDocumentUser2Avatar(document)} count={document.users_data.length} />}
      >
        {document.reference && (
          <div>
            <span className="fw-bold me-1">Reference:</span>
            <span className="reference">{document.reference}</span>
          </div>
        )}
        {document.approved_by && (
          <div>
            <span className="fw-bold me-1">Apprové par:</span>
            <span className="approver">{document.approved_by.name}</span>
          </div>
        )}
        {document.archived_by && (
          <div>
            <span className="fw-bold me-1">Archivé par:</span>
            <span className="archiver">{document.archived_by.name}</span>
          </div>
        )}

        {document.description && <div className="description mt-3">{document.description}</div>}
      </AdminView.Section>

      {Boolean(document.files_data.length) && (
        <AdminView.Section title="Fichiers">
          <DocuFileGrid items={document.files_data} />
        </AdminView.Section>
      )}
    </AdminView>
  );
}

DocumentReadOnlyView.propTypes = propTypes;
