import { Icons, Button, ImgSquare, Img, ConfirmIconButton } from "@miq/components";
import { getImgUrl } from "@miq/utils";
import PdfViewer from "@miq/pdfjs";

import "./docufile-grid.scss";

const getIcon = (file) => {
    switch (file.ext) {
        case ".jpg":
        case ".jpeg":
        case ".png":
            return <ImgSquare {...file} />;

        default:
            const Icon = Icons.getFileIcon(file.ext);
            return <Icon className="icon mb-1" />;
    }
};

export default function DocuFileGrid({ items = [], ...props }) {
    const renderPreview = ({ file, isImg }) => {
        return (
            <div className="miq-container" style={{ minHeight: 500 }}>
                {isImg && <Img {...file} />}
                {file.ext === ".pdf" && <PdfViewer.Renderer />}
            </div>
        );
    };

    const renderPreviewHeader = ({ setOpen, file }) => (
        <div className="flex-1 d-flex justify-content-between">
            <div className="" style={{ overflowX: "scroll" }}>
                {file.name_truncated}
            </div>

            <div className="flex-1">
                <Button className="me-1">Delete</Button>
                <Button onClick={() => setOpen(false)}>Close</Button>
            </div>
        </div>
    );

    const renderPreviewFooter = ({ file }) => {
        if (file.ext !== ".pdf") return null;

        return (
            <div className="flex-1 miq-container d-flex align-items-center justify-content-between">
                <div>
                    <PdfViewer.Previous />
                </div>
                <div>
                    <PdfViewer.Next />
                </div>
            </div>
        );
    };
    return (
        <div className="docufile-grid d-grid grid-2 grid-sm-3 grid-md-4 grid-xl-5">
            {items.map((file) => {
                // const isImg = file.ext === ".jpg" || file.ext === ".jpeg" || file.ext === ".png";

                return (
                    // <PdfViewer src={file.src} >
                    //     <ConfirmIconButton
                    //         fullscreen
                    //         title={file.name}
                    //         renderHeader={(args) => renderPreviewHeader({ ...args, file })}
                    //         render={(args) => renderPreview({ ...args, file, isImg })}
                    //         renderFooter={(args) => renderPreviewFooter({ ...args, file })}
                    //     >

                    <a href={getImgUrl(file.src)} className="docufile-btn" target="_blank" key={file.slug}>
                        {getIcon(file)}

                        <div className="text-center">
                            <span className="label d-block">{file.name_truncated}</span>
                            <span className="size d-block">{file.size}</span>
                        </div>
                    </a>
                    //     </ConfirmIconButton>
                    // </PdfViewer>
                );
            })}
        </div>
    );
}
